import { useState, useEffect } from "react";
import Masonry from 'react-masonry-css'
import {Link} from 'react-router-dom'
const breakpointColumnsObj = {
  default: 4,
  1100: 3,
  700: 2,
  500: 1
};
const query = `
query {
  componentCollection(limit:10) {
    items {
      sys{
        id
      }
      title
      cover {
        title
        url(transform:{width:300})
      }
      description
    }
  }
}
`;
function Posts() {
    const [page, setPage] = useState(null);
    var url='https://graphql.contentful.com/content/v1/spaces/7qehr25il115/';

    useEffect(() => {
    window.fetch(url, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: "Bearer EJzVdK0T5u2t9tTDjsJmB8cBu8GFZ4Khq3mPirM6PnY",
        },
        body: JSON.stringify({ query }),
      })  .then((response) => response.json())
      .then(({ data, errors }) => {
        console.log(data)
         setPage(data.componentCollection.items);
         
      });
  }, []);

    if (!page) {
      return <div class="App-header"><center>loading...</center></div>;
    }

    return (
        <div class="App-header">

           <Masonry breakpointCols={breakpointColumnsObj} className="grid"  columnClassName="grid-column">
           {page.map((index)=>(

                              
              <div key={index} className="card"> 
                <img className="heroImage" src={index.cover.url}/>
                <div class="card-content">
                  <h1>
                  <Link to={"/post/" + index.sys.id}>{index.title}</Link>   
                  </h1>
                  <p>
                      {index.description}
                  </p>
                </div>
              </div>

              ))}
           </Masonry>

        </div>
    )
}


export default Posts;