import {useParams} from 'react-router-dom'
import { useState, useEffect } from "react";
import { documentToReactComponents } from '@contentful/rich-text-react-renderer';
import {Helmet} from "react-helmet";



function Post() {
    let {id} = useParams();
    const [page, setPage] = useState(null);
    var url='https://graphql.contentful.com/content/v1/spaces/7qehr25il115/';
    const query = `
    query {
        component(id: "${id}") {
          sys {
            id
          }
          title
          cover {
            url(transform: { width: 1200 })
          }
          description
          content {
            json
          }
          researchCollection {
            items {
              title
              size
              url
            }
          }
          galleryCollection {
            items {
              url (transform: { width: 600 })
            }
          }
        }
      }
      
`;
    useEffect(() => {
    window.fetch(url, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: "Bearer EJzVdK0T5u2t9tTDjsJmB8cBu8GFZ4Khq3mPirM6PnY",
        },
        body: JSON.stringify({ query }),
      })  .then((response) => response.json())
      .then(({ data, errors }) => {
        console.log(data)
         setPage(data.component);
         
      });
  }, []);

    if (!page) {
      return <div className="App-header"><center>loading...</center></div>;
    }
    return (
        <article>
          <Helmet>
              <meta charSet="utf-8" />
              <title>{page.title}</title>
              <meta
              name="description"
              content={page.description}
            />
              <meta property="og:url" content={"https://maxengine.ngot.work/post/" + page.sys.id } />
              <meta property="og:type" content="website" />
              <meta property="og:title" content={page.title}/>
              <meta property="og:description" content={page.description}/>
              <meta property="og:image" content={page.cover.url} />
          </Helmet>
           <h1>{page.title}</h1>
           <img src={page.cover.url}/>
           <div className="description">
               {page.description !==null 
               ? page.description
               : "null"
            }
           </div>
           <div className="content">
               {page.content !==null 
               ? documentToReactComponents(page.content.json)
               : "null"
            }
           </div>
           <div className="research">
               {page.researchCollection !==null 
               ? page.researchCollection.items.map((item)=>(
                   <div key={item}>
                       <button type="button" href={item.url}>{item.title}</button>
                   </div>
               ))
               : "null"
            }
           </div>
           <div className="research">
               {page.galleryCollection !==null 
               ? page.galleryCollection.items.map((item)=>(
                   <img src={item.url} key={item}/>
               ))
               : "null"
            }
           </div>
        </article>
    )
}

export default Post;